const mobileMenu = (triggerSelector, triggerClose, selectorParent, classBtnClose, classOverlay) => {
const trigger = document.querySelector(triggerSelector),
    wrapperMenu = document.querySelector(trigger.dataset.target),
    overlay = document.querySelector(`${trigger.dataset.target}-overlay`),
    close = wrapperMenu.querySelector(triggerClose),
    parent = wrapperMenu.querySelectorAll(selectorParent)
   let level = 1
    trigger.addEventListener('click', () => {
        wrapperMenu.classList.add('show')
        overlay.style.display = 'block'
        document.querySelector('body').style.overflow = 'hidden'
    })

    function closed (classBtnClose, classOverlay) {
    document.addEventListener('click', (e) => {
        if(e.target.classList && e.target.classList.contains(classBtnClose) || e.target.classList && e.target.classList.contains(classOverlay)){
            level = 1
            wrapperMenu.classList.remove('show')
            overlay.style.display = 'none'
            parent.forEach(item => {
                item.classList.remove('expanded')
            })
            wrapperMenu.querySelector('.wrap').style.transform = 'none'
            document.querySelector('body').style.overflow = ''
        }
    })
    }

/*    close.addEventListener('click', () => {
        if(wrapperMenu.classList.contains('show')) {
            wrapperMenu.classList.remove('show')
            overlay.style.display = 'none'
        }
    })*/

document.addEventListener('click', (e) => {


    if((e.target.classList && e.target.nodeName === "SPAN" && e.target.classList.contains('arrow')) ||
        (e.target.classList && e.target.nodeName === "I" && e.target.classList.contains('alb-svg'))
    ){
        e.preventDefault()
        const parent = e.target.closest('li.parent')
        const wrap = parent.closest('.wrap')
        parent.classList.add('expanded')
            wrap.style.transform = `translateX(-${level  * 100}%)`
        level++
    }else if(e.target.classList && e.target.nodeName === "I" && e.target.classList.contains('back_link') ||
        e.target.classList && e.target.nodeName === "A" && e.target.classList.contains('back_link')
    ){
        e.preventDefault()
        level--
        e.target.closest('li.parent').classList.remove('expanded')
        e.target.closest('.wrap').style.transform = `translateX(-${(level - 1 ) * 100}%)`

    }
})

   closed(classBtnClose, classOverlay)
}

export default mobileMenu
