const searchAccordion = (selectorTrigger, selectorBlock) => {
    const trigger = document.querySelector(selectorTrigger)
    const block = document.querySelector(selectorBlock)


    function handlerClick() {
        trigger.addEventListener('click', (e) => {
           if(block.classList.contains('d-none')){
               block.classList.remove('d-none')
               block.classList.add('d-flex')
           }else {
               block.classList.remove('d-flex')
               block.classList.add('d-none')
           }
        })
    }

    handlerClick()
}

export default searchAccordion
