

$(function () {

$('.lincUp').on('click', (e) => {
   if($(e.target).prev().hasClass('minWindow')){

       $(e.target).text('').prev().removeClass('minWindow').addClass('bigWindow')
       $(".bigWindow").niceScroll({
           cursorcolor:'rgba(178,183,187,0.65)',
           cursorminheight: 23,
       });

   }else {
      $(e.target).text(' ').prev().removeClass('bigWindow').addClass('minWindow')

       $(".minWindow").getNiceScroll().remove()
       $(".minWindow").attr('style', '')


   }

})
})
